<template>

    <div class="app">
        <div class="searchBox">
            <div class="searchInputBox">
                <i></i>
                <input v-model="room_number" @change="getList" type="text" placeholder="搜索">
            </div>
        </div>
        <ul  v-if="listData.length" class="moneyList">
            <li v-for="item in listData">
                <router-link class="ov" :to="{path:'/redchat', query:{groupId:item.id}}">
                    <div class="fl headImg"><img :src="item.currency.icon" alt=""></div>
                    <div class="fl infoBox">
                        <p class="nameBox"><span class="name">{{item.money}}个币{{item.count}}个包</span></p>
                        <p class="time">{{item.send_rule == 1 ? '最小' : '最大'}}的发</p>
                    </div>
                    <div class="fr eosNumber">
                        <p class="number">成员数{{item.user_count}}</p>
                        <p class="less">加入</p>
                    </div>
                </router-link>
            </li>

        </ul>
        <div v-if="!listData.length" class="searchresult">
            <img src="@/static/images/tab/wujilv_image.png" alt="">
            <p>空空如也</p>
        </div>
    </div>
</template>

<script>
    import { searchRed } from "@/api/index"
    import Toast from 'vant/lib/toast';
    import 'vant/lib/toast/style';

    export default {
        name: "Searchred",
        data()
        {
            return {
                room_number:'',
                listData:[]
            }
        },
        mounted()
        {

        },
        methods:{

            // 获取列表
            getList()
            {
                searchRed(this.room_number).then(res => {

                    this.listData = res
                })
            }
        }
    }
</script>

<style scoped>
    .app .searchBox {
        overflow: hidden;
        padding: 0.1rem 0.15rem;
        background: #f5f5f5;
    }
    .app .searchBox .searchInputBox {
        float: left;
        background: #fff;
        width: 3.45rem;
        border-radius: 0.02rem;
        position: relative;
    }
    .app .searchBox .searchInputBox i {
        background: url("../../static/images/my/btn_search_gray.png");
        background-size: 100%;
        width: 0.16rem;
        height: 0.16rem;
        position: absolute;
        left: 0.11rem;
        top: 0.11rem;
    }
    .app .searchBox .searchInputBox input {
        height: 0.34rem;
        margin-left: 0.32rem;
        float: left;
        width: 80%;
        font-size: 0.14rem;
        color: #8E8E93;
    }
    .app .moneyList li {
        border-bottom: 0.01rem solid #F5F5F5;
    }
    .app .moneyList li a {
        display: block;
        padding: 0.15rem;
    }
    .app .moneyList li a .headImg {
        width: 0.45rem;
        height: 0.45rem;
        /*background: #FF7743;*/
        /*border-radius: 0.02rem;*/
        margin-right: 0.1rem;
    }
    .app .moneyList li a .headImg img {

        width: 100%;
        height: 100%;
        border-radius: 50%;
    }
    .app .moneyList li a .headImg p {
        text-align: center;
        color: #fff;
        font-size: 0.1rem;
        line-height: 0.45rem;
    }
    .app .moneyList li a .infoBox .nameBox .name {
        color: #404040;
        font-size: 0.14rem;
    }
    .app .moneyList li a .infoBox .time {
        font-size: 0.1rem;
        color: #999999;
        margin-top: 0.05rem;
    }
    .app .moneyList li a .eosNumber {
        text-align: right;
    }
    .app .moneyList li a .eosNumber .number {
        font-size: 0.12rem;
        color: #404040;
    }
    .app .moneyList li a .eosNumber .less {
        color: #FFFFFF;
        font-size: 0.1rem;
        padding: 0.01rem 0.06rem;
        background: #FABD69;
        display: inline-block;
        border-radius: 0.03rem;
        margin-top: 0.05rem;
    }

    .searchresult {
        height: 70vh;
        text-align: center;
    }
    .searchresult img {
        width: 1.1rem;
        height: 1rem;
        display: block;
        margin: auto;
        padding-top: 2rem;
    }
    .searchresult p {
        font-size: 0.14rem;
        color: #666666;
        margin-top: 0.2rem;
    }
</style>



